import { DatePipe } from "@angular/common";
import { Component, Input, OnInit } from "@angular/core";
import { NbToastrService } from "@nebular/theme";
import { ProductService } from "../../../../../@core/app-services/product.service";
import { AddProductModel } from "../../../../../@core/models/addProductModel";
import { Brand } from "../../../../../@core/models/brand";
import { Category } from "../../../../../@core/models/category";
import { Grade } from "../../../../../@core/models/grade";
import { Product } from "../../../../../@core/models/product";

@Component({
  selector: "product-basic-info",
  templateUrl: "./product-basic-info.component.html",
  styleUrls: ["./product-basic-info.component.scss"],
})
export class ProductBasicInfoComponent implements OnInit {
  @Input() productId: number;
  productBasicInfo: Product;
  grades: Grade[] = new Array();
  brands: Brand[] = new Array();
  categories: Category[] = new Array();
  loading: boolean = false;
  createdAt: string;
  updatedAt: string;
  selectedProductType: string;
  addOrUpdateModel: AddProductModel = new AddProductModel();
  constructor(
    private productService: ProductService,
    private datepipe: DatePipe,
    private toastrService: NbToastrService
  ) {
    this.productService.getBrands().subscribe((data) => {
      this.brands = data;
    });
    this.productService.getGrades().subscribe((data) => {
      this.grades = data;
    });
    this.productService.getCategories().subscribe((data) => {
      this.categories = data;
    });
  }

  ngOnInit() {}

  saveChanges() {
    this.addOrUpdateModel.brandId = this.productBasicInfo.brand.brandId;
    this.addOrUpdateModel.categoryId = this.productBasicInfo.category.id;
    this.addOrUpdateModel.description = this.productBasicInfo.description;
    this.addOrUpdateModel.features = this.productBasicInfo.features.map(({ id }) => id);
    this.addOrUpdateModel.gradeId = this.productBasicInfo.grade.id;
    this.addOrUpdateModel.image = this.productBasicInfo.image;
    this.addOrUpdateModel.price = this.productBasicInfo.price;
    this.addOrUpdateModel.title = this.productBasicInfo.productName;
    this.productService
      .addOrUpdateProduct(this.addOrUpdateModel)
      .subscribe((data) => {
        if (data.status == true) {
          this.toastrService.show(
            "Product Info Updated Successfully",
            "Updated",
            { status: "success", duration: 5000 }
          );
        }
      });
  }
}
