import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from '../../../environments/environment';
import { AddProductModel } from '../models/addProductModel';
import { Brand } from '../models/brand';
import { Category } from '../models/category';
import { Feature } from '../models/feature';
import { Grade } from '../models/grade';
import { Product } from '../models/product';


@Injectable({
  providedIn: 'root'
})
export class ProductService {

  private getBrandsPath = environment.apiUrl + 'Products/GetBrands';
  private getGradesPath = environment.apiUrl + 'Products/GetGrades';
  private getCategoriesPath = environment.apiUrl + 'Products/GetCategories';
  private getFeaturesPath = environment.apiUrl + 'Products/GetFeatures';
  private getProductsByBrandPath = environment.apiUrl + 'Products/getProductsByBrand?brandId=';
  private getProductsByCategoryPath = environment.apiUrl + 'Products/getProductsByCategory?categoryId=';
  private searchForProductPath = environment.apiUrl + 'Products/searchForProduct?search=';
  private addProductPath = environment.apiUrl + 'Products/addOrUpdateProduct';
  

  constructor(private http: HttpClient) { }

  
  getBrands(): Observable<Brand[]> {
    return this.http.get<Brand[]>(this.getBrandsPath);
  }
  getGrades(): Observable<Grade[]> {
    return this.http.get<Grade[]>(this.getGradesPath);
  }
  getCategories(): Observable<Category[]> {
    return this.http.get<Category[]>(this.getCategoriesPath);
  }
  getFeatures(): Observable<Feature[]> {
    return this.http.get<Feature[]>(this.getFeaturesPath);
  }
  getProductsByBrand(brandId:number): Observable<Product[]> {
    return this.http.get<Product[]>(this.getProductsByBrandPath+brandId);
  }
  getProductsByCategory(categoryId:number): Observable<Product[]> {
    return this.http.get<Product[]>(this.getProductsByCategoryPath+categoryId);
  }
  searchForProduct(search:string, page:number): Observable<Product[]> {
    return this.http.get<Product[]>(this.searchForProductPath+search+'&page='+page);
  }
  addOrUpdateProduct(model:AddProductModel): Observable<any> {
    return this.http.post<any>(this.addProductPath,model);
  }

}
